.Profile {
    width: 100%;
    height: 84%;
}

.Profile-Header {
    display: flex;
    align-items: center;
    width: 100%;
    height: 10%;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.542);
}

.Profile .Profile-Header .Profile-Numbers {
    display: flex;
    width: 75%;
    justify-content: space-around;
    margin-left: 0.5rem;
}

.Profile .Profile-Followers {
    text-align: center;
    width: 20%;
}

.Profile .Profile-Following {
    text-align: center;
    width: 20%;
}

.Profile .Profile-Catches {
    text-align: center;
    width: 20%;
}

.Profile .Profile-Followers p {
    margin: 0;
}


.Profile .Profile-Following p {
    margin: 0;
}

.Profile .Profile-Catches p {
    margin: 0;
}

.Profile .Profile-Header .P-Bold {
    font-size: 1.3rem;
    font-weight: 600;
}

.Profile .Profile-Picture {
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
}

.Profile .Profile-Picture img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.Profile .Profile-Cards {
    border-top: 2px solid rgba(255, 255, 255, 0.8);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}