.Footer {
    height: 8%;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    align-items: center;
    justify-items: center;
    position: sticky;
    z-index: 1000;
    background: rgba(255, 255, 255, 0.8);
    border-top: 1px solid rgba(128, 128, 128, 0.582);
    margin-top: auto;
}

.Footer .Upload-Button {
    height: 3rem;
    width: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 2rem;
}

.Footer .Upload-Logo {
    font-size: 1.5rem;
    border: none;
    color: black;
}

.Footer-Button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
}

.Footer-Button p {
    font-size: 2vh;
    margin: 0;
}